.boton {
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-weight: bolder;
    color: #fff;
    text-align: center;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    text-shadow: 0px 0px 16px black;
}
.boton span {
     width: 100%;
     position: absolute;
     top:15px;
     left:0;
 }

.boton svg line {
    stroke-width: 6;
    stroke: #fff;
    fill: none;

    -webkit-transition: all 600ms ease-in-out;
    transition: all 600ms ease-in-out;
}
.boton:hover svg line {
    stroke: #76ff03;
}
 
.boton:hover svg line.L-T {
  -webkit-transform: translateY(50px);
  transform: translateY(50px);
}
.boton:hover svg line.L-B {
  -webkit-transform: translateY(-50px);
  transform: translateY(-50px);
}
div.boton:hover svg line.R-T {
  -webkit-transform: translateY(50px);
  transform: translateY(50px);
}
.boton:hover svg line.R-B {
  -webkit-transform: translateY(-50px);
  transform: translateY(-50px);
}
.boton:hover svg line.T-L {
  -webkit-transform: translateX(270px);
  transform: translateX(270px);
}
.boton:hover svg line.T-R {
  -webkit-transform: translateX(-270px);
  transform: translateX(-270px);
}
.boton:hover svg line.B-L {
  -webkit-transform: translateX(270px);
  transform: translateX(270px);
}
.boton:hover svg line.B-R {
  -webkit-transform: translateX(-270px);
  transform: translateX(-270px);
}


